import React, { useState } from 'react';

// material-ui
import {
    Avatar,
    CardContent,
    CardActions,
    Card,
    Stack,
    Typography,
    Container,
    Chip,
    Grid,
    Button,
    Paper,
    TextField,
    ListItemAvatar,
    ListItemButton,
    ListItemSecondaryAction,
    Tooltip
} from '@mui/material';

import AddIcon from '@mui/icons-material/Add';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';

import List from '@mui/material/List';
import ListItemText from '@mui/material/ListItemText';
import IconButton from '@mui/material/IconButton';
import {
    StockOutlined,
    FireOutlined,
    HighlightOutlined,
    LaptopOutlined,
    QuestionCircleOutlined,
    ContactsOutlined,
    ContainerOutlined,
    ApartmentOutlined
} from '@ant-design/icons';

import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

import Dialog from '@mui/material/Dialog';
import Slide from '@mui/material/Slide';
import Autocomplete from '@mui/material/Autocomplete';

import Dropzone from 'react-dropzone';
import { useNavigate } from 'react-router';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { useEffect } from 'react';
import axios from 'axios';
import { apiBaseURL } from 'src/config-global';
import { useContext } from 'react';
import { AuthContext } from 'src/App';
import { toast } from 'react-toastify';
import EmployeeTaskForm from './EmployeeTaskForm';
import { useAuthContext } from 'src/auth/useAuthContext';

// avatar style
const avatarSX = {
    width: 36,
    height: 36,
    fontSize: '1rem'
};

// action style
const actionSX = {
    mt: 0.75,
    ml: 1,
    top: 'auto',
    right: 'auto',
    alignSelf: 'flex-start',
    transform: 'none'
};

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="left" ref={ref} {...props} />;
});

const OPTION_OPERATION = [
    // {
    //     title: 'Report system issue or incident',
    //     desc: "Use this form when an entire system or part of application is unavailable and can't be accessed by the user",
    //     icon: <StockOutlined />
    // },
    // {
    //     title: 'Report a problem with a product bug',
    //     desc: "Use this form to  report a problem  with a product that accessible but not fully functional, such as being able to submit a request for a report but it takes longer than normal or dosen't appear to be corrected",
    //     icon: <FireOutlined />
    // },
    // {
    //     title: 'Request system or product access',
    //     desc: 'Use this form to request access to system or product',
    //     icon: <HighlightOutlined />
    // },
    // {
    //     title: 'Technology Intake Form',
    //     desc: 'Use this form to request new server build, Server Documentation, New Project, Project Change Request (PCR)...etc',
    //     icon: <LaptopOutlined />
    // },
    // {
    //     title: 'Submit a request for information or PE assistance',
    //     desc: 'Use this form to request the information or assistance for PE manageed platforms.',
    //     icon: <QuestionCircleOutlined />
    // },
    {
        title: 'Onboarding form',
        desc: 'Use this form to submit requested information for new hires.',
        icon: <ContactsOutlined />,
        url: 'onboardingform'
    },
    {
        title: 'Offboarding form',
        desc: 'Use this form to submit request information for onboarded users.',
        icon: <ContainerOutlined />,
        url: 'offboardingform'
    },
    {
        title: 'Employee Onboarding Task',
        desc: "Use this form to submit tasks to new hires.",
        icon: <StockOutlined />,
        url: '#'
    },
    {
        title: 'Track Onboardings',
        desc: 'Track onboarding requests made by / to you',
        icon: <ApartmentOutlined />,
        url: 'trackonboarding'
    }
];

const ManagerPortal = () => {
    const navigate = useNavigate();
    const { user, getDataByRole } = useAuthContext();

    function getRandomInt(max) {
        return Math.floor(Math.random() * max);
    }
    const [open, setOpen] = React.useState(false);
    const [title, settitle] = React.useState('');
    const [employeeList, setEmployeeList] = useState(null);

    const handleClickOpen = (formTitle) => {
        setOpen(true);
        settitle(formTitle);
    };
    const handleClose = () => {
        setOpen(false);
    };

    const [serverity, setserverity] = React.useState('High');
    const handleChange = (event) => {
        setserverity(event.target.value);
    };

    const [empTask, setEmpTask] = useState({
        employee: 'None',
        task: '',
        source: '',
        url: '',
        tag: ' ',
        due: new Date().toLocaleDateString()
    });
    const handleEmpTask = (field) => (e) => {
        let value;
        if (field === 'due') value = e;
        else value = e.target.value;
        setEmpTask((prev) => ({
            ...prev,
            [field]: value
        }));
    };

    const handleDialogSubmit = async () => {
        if (title === 'Employee Onboarding Task') {
            const formData = {
                ...empTask,
                managerId: user.uid,
                company: user.company,
                manager: user.displayName
            };
            const res = await axios.post(`${apiBaseURL}/manager/emptask`, { formData });
            if (res.data === 'Created') toast.success(`Task Assigned to ${formData.employee}`);
            handleClose();
        }
    };

    useEffect(() => {
        if (!user) return;
        const data = getDataByRole('employee');
        setEmployeeList(data);
    }, [user]);

    return (
        <>
            <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition}>
                <Container sx={{ mt: 6, mb: 6 }} maxWidth="sm">
                    <Card variant="outlined">
                        <CardContent>
                            <div style={{ display: 'flex', justifyContent: 'center' }}>
                                <Typography variant="h2">{title}</Typography>
                            </div>
                            <br />
                            {title === 'On boarding employee' ? (
                                <>
                                    <Typography variant="h4">Employee Details</Typography>
                                    <br />
                                    <Stack direction="row" spacing={2}>
                                        <div>
                                            <Typography variant="p">
                                                First Name <span style={{ color: 'red' }}>*</span>
                                            </Typography>
                                            <TextField type="text" variant="outlined" fullWidth />
                                        </div>
                                        <div>
                                            <Typography variant="p">Middle Name</Typography>
                                            <TextField type="text" variant="outlined" fullWidth />
                                        </div>
                                        <div>
                                            <Typography variant="p">
                                                Surame <span style={{ color: 'red' }}>*</span>
                                            </Typography>
                                            <TextField type="text" variant="outlined" fullWidth />
                                        </div>
                                    </Stack>
                                    <br />
                                    <Typography variant="p">Contact Number</Typography>
                                    <TextField type="text" variant="outlined" fullWidth />
                                    <br />
                                    <br />
                                    <Stack direction="row" spacing={2}>
                                        {/* <FormControl fullWidth>
                                            <InputLabel id="country-location-label">Country Location</InputLabel>
                                            <Select
                                                labelId="country-location-label"
                                                id="country-location"
                                                value={serverity}
                                                onChange={handleChange}
                                            >
                                                <MenuItem value="High">High</MenuItem>
                                                <MenuItem value="Medium">Medium</MenuItem>
                                                <MenuItem value="Low">Low</MenuItem>
                                            </Select>
                                        </FormControl>
                                        <FormControl fullWidth>
                                            <InputLabel id="office-location-label">Office Location</InputLabel>
                                            <Select
                                                labelId="office-location-label"
                                                id="office-location"
                                                value={serverity}
                                                onChange={handleChange}
                                            >
                                                <MenuItem value="High">High</MenuItem>
                                                <MenuItem value="Medium">Medium</MenuItem>
                                                <MenuItem value="Low">Low</MenuItem>
                                            </Select>
                                        </FormControl> */}

                                        <div style={{ width: '50%' }}>
                                            <Typography variant="p">
                                                Country <span style={{ color: 'red' }}>*</span>
                                            </Typography>
                                            <TextField type="text" variant="outlined" fullWidth />
                                        </div>
                                        <div style={{ width: '50%' }}>
                                            <Typography variant="p">
                                                Office Location <span style={{ color: 'red' }}>*</span>
                                            </Typography>
                                            <TextField type="text" variant="outlined" fullWidth />
                                        </div>
                                    </Stack>
                                    <br />
                                    <Stack direction="row" spacing={2}>
                                        <div style={{ width: '50%' }}>
                                            <Typography variant="p">
                                                Business Unit <span style={{ color: 'red' }}>*</span>
                                            </Typography>
                                            <TextField type="text" variant="outlined" fullWidth />
                                        </div>
                                        <div style={{ width: '50%' }}>
                                            <Typography variant="p">
                                                Start Date <span style={{ color: 'red' }}>*</span>
                                            </Typography>
                                            <TextField
                                                // InputLabelProps={{
                                                //     shrink: true
                                                // }}
                                                variant="outlined"
                                                type="date"
                                                fullWidth
                                            />
                                        </div>
                                    </Stack>
                                    <br />
                                    <Typography variant="h4">Position Details</Typography>
                                    <br />
                                    <Stack direction="row" spacing={2}>
                                        <div style={{ width: '50%' }}>
                                            <Typography variant="p">
                                                Manager <span style={{ color: 'red' }}>*</span>
                                            </Typography>
                                            <TextField type="text" variant="outlined" fullWidth />
                                        </div>
                                        <div style={{ width: '50%' }}>
                                            <Typography variant="p">
                                                Job Title <span style={{ color: 'red' }}>*</span>
                                            </Typography>
                                            <TextField type="text" variant="outlined" fullWidth />
                                        </div>
                                    </Stack>
                                    <br />
                                    <Typography variant="p">
                                        Business Unit <span style={{ color: 'red' }}>*</span>
                                    </Typography>
                                    <TextField type="text" variant="outlined" fullWidth />
                                </>
                            ) : title === 'Off boarding new employee' ? (
                                <>
                                    <Typography variant="p">Employee Name</Typography>
                                    <TextField type="text" variant="outlined" fullWidth />
                                    <br />
                                    <br />

                                    <TextField
                                        label="End Date"
                                        InputLabelProps={{
                                            shrink: true
                                        }}
                                        variant="outlined"
                                        type="date"
                                        fullWidth
                                    />
                                </>
                            ) : title === 'Attachment' ? (
                                <>
                                    <Typography variant="p">Attachment</Typography>
                                    <Dropzone onDrop={(acceptedFiles) => console.log(acceptedFiles)}>
                                        {({ getRootProps, getInputProps }) => (
                                            <section style={{ border: '2px dotted', padding: '10px' }}>
                                                <center>
                                                    <div {...getRootProps()}>
                                                        <input {...getInputProps()} />
                                                        <p>Drag 'n' drop some files here, or click to select files</p>
                                                    </div>
                                                </center>
                                            </section>
                                        )}
                                    </Dropzone>
                                    <br />
                                    <Typography variant="p">Summary</Typography>
                                    <TextField type="text" variant="outlined" fullWidth />

                                    <br />
                                    <br />
                                    <Typography variant="p">Product Name*</Typography>
                                    <TextField type="text" variant="outlined" fullWidth />

                                    <br />
                                    <br />
                                    <FormControl sx={{ mt: 1 }} fullWidth>
                                        <InputLabel id="demo-simple-select-label">Serverity</InputLabel>
                                        <Select
                                            labelId="demo-simple-select-label"
                                            id="demo-simple-select"
                                            value={serverity}
                                            label="Serverity"
                                            onChange={handleChange}
                                        >
                                            <MenuItem value="High">High</MenuItem>
                                            <MenuItem value="Medium">Medium</MenuItem>
                                            <MenuItem value="Low">Low</MenuItem>
                                        </Select>
                                    </FormControl>

                                    <br />
                                    <br />
                                    <Typography variant="p">Description*</Typography>
                                    <TextField variant="outlined" fullWidth type="text" multiline rows={6} />

                                    <br />
                                    <br />
                                    <Typography variant="p">Steps to Reproduce*</Typography>
                                    <TextField variant="outlined" type="text" fullWidth multiline rows={6} />
                                    <br />
                                    <br />
                                </>
                            ) : title === 'Employee Onboarding Task' ? (
                                <>
                                    <EmployeeTaskForm empTask={empTask} employeeList={employeeList} handleEmpTask={handleEmpTask} />
                                </>
                            ) : title === 'Report a problem with a product bug' ? (
                                <>
                                    <Typography variant="p">Due Date</Typography>
                                    <br />
                                    <TextField variant="outlined" type="date" />
                                </>
                            ) : null}
                        </CardContent>

                        <CardActions sx={{ float: 'right' }} style={{ padding: '0px 20px 20px' }}>
                            <Button onClick={handleClose}>Close</Button>
                            <Button onClick={handleDialogSubmit} variant="contained">
                                Submit
                            </Button>
                        </CardActions>
                    </Card>
                </Container>
            </Dialog>

            <Grid container rowSpacing={4.5} columnSpacing={2.75}>
                <Grid item xs={12}>
                    <Typography sx={{ float: 'left' }} variant="h3">
                        Manager Portal
                    </Typography>
                </Grid>

                <Grid item xs={12}>
                    <Container>
                        <List
                            component="nav"
                            sx={{
                                px: 0,
                                py: 0,
                                '& .MuiListItemButton-root': {
                                    py: 1.5,
                                    '& .MuiAvatar-root': avatarSX,
                                    '& .MuiListItemSecondaryAction-root': { ...actionSX, position: 'relative' }
                                }
                            }}
                        >
                            {OPTION_OPERATION.map((item) => {
                                return (
                                    <ListItemButton
                                        disabled={item.title === 'Employee Onboarding Task' ? !employeeList : false}
                                        key={item.url}
                                        onClick={() =>
                                            item?.title === 'Employee Onboarding Task'
                                                ? handleClickOpen(item?.title)
                                                : navigate(`/dashboard/${item.url}`)
                                        }
                                        divider
                                    >
                                        <ListItemAvatar>
                                            <Avatar
                                                sx={{
                                                    color: 'primary.main',
                                                    bgcolor: 'primary.lighter'
                                                }}
                                            >
                                                {item?.icon}
                                            </Avatar>
                                        </ListItemAvatar>
                                        <ListItemText
                                            primary={<Typography variant="subtitle1">{item?.title}</Typography>}
                                            secondary={item?.desc}
                                        />
                                    </ListItemButton>
                                );
                            })}
                        </List>
                    </Container>
                </Grid>
            </Grid>
        </>
    );
};

export default ManagerPortal;
