import React from 'react';
import { FormControl, Grid, MenuItem, Select, TextField, Typography } from '@mui/material';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';

const EmployeeTaskForm = ({ empTask, employeeList, handleEmpTask }) => {
    return (
        <Grid container spacing={2}>
            <Grid item xs={6}>
                <Typography variant="p">Select Employee</Typography>
                <br />
                <FormControl fullWidth>
                    <Select fullWidth id="demo-simple-select" value={empTask.employee} onChange={handleEmpTask('employee')}>
                        {employeeList ? <MenuItem value="None">None</MenuItem> : <MenuItem value="None">No employee found</MenuItem>}
                        {employeeList &&
                            employeeList.map((emp, index) => (
                                <MenuItem key={emp.email} value={emp.email}>
                                    {emp.email}
                                </MenuItem>
                            ))}
                    </Select>
                </FormControl>
            </Grid>
            <Grid item xs={6}>
                <Typography variant="p">Task Name</Typography>
                <TextField variant="outlined" fullWidth type="text" value={empTask.task} onChange={handleEmpTask('task')} />
            </Grid>
            <Grid item xs={6}>
                <Typography variant="p">Source Name</Typography>
                <TextField variant="outlined" fullWidth type="text" value={empTask.source} onChange={handleEmpTask('source')} />
            </Grid>
            <Grid item xs={6}>
                <Typography variant="p">Source URL (If applicable *)</Typography>
                <TextField variant="outlined" fullWidth type="text" value={empTask.url} onChange={handleEmpTask('url')} />
            </Grid>
            {/* <Grid item xs={6}>
                <Typography variant="p">Tag Name</Typography>
                <TextField variant="outlined" fullWidth type="text" value={empTask.tag} onChange={handleEmpTask('tag')} />
            </Grid> */}
            <Grid item xs={6}>
                <Typography variant="p">Due Date</Typography>
                <br />
                <LocalizationProvider dateAdapter={AdapterDayjs}>
                    <DatePicker
                        disablePast
                        openTo="year"
                        views={['year', 'month', 'day']}
                        value={empTask.due}
                        onChange={handleEmpTask('due')}
                        renderInput={(params) => <TextField {...params} />}
                    />
                </LocalizationProvider>
            </Grid>
        </Grid>
    );
};

export default EmployeeTaskForm;
